<!--  -->
<template>
  <div class='header'>
    <div class="fatherlayout">
      父组件
      <el-row>
        <el-button size='mini' type="primary" @click="byvalue">点击</el-button>
      </el-row>
      <div>{{values}}</div>
    </div>
    <div class="sonclass">
      以下子组件中的内容--------------------
      <!-- show子组件自定义方法 yyif父组件接收数据 -->
      <son :names="name" :dynamic="dynamic" @show="yyif"></son>

    </div>
    <navs></navs>
  </div>
</template>

<script>
  import Navs from '../navs.vue';
  import son from './son.vue';
  export default {
    components: {
      son,
      Navs
    },
    name: '',
    data() {
      //这里存放数据
      return {
        name: "父组件中的内容/值",
        // 动态传值
        dynamic: '',
        // 来自己子组件的值
        values: ""
      };
    },
    //方法集合
    methods: {
      byvalue() {
        this.dynamic = '点击父组件传来的值'
      },
      // 自定义方法
      yyif(sondata) {
        console.log(sondata)
        this.values = sondata
      }
    },
  }

</script>
<style lang='less' scoped>
    .fatherlayout,
    .sonclass {
      width: 100%;
      height: 200px;
      background: skyblue;
      text-align: center;
      margin-bottom: 5px;
    }


</style>



